.header {
    background-color: $blanco;
    h1 {
        text-align: center;
        font-weight: $bold;
        line-height: 2;
        margin: 0;
    }
}

.barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
    }
    img {
        max-width: 20rem;
    }
}

.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    @include tablet {
        align-items: flex-end;
    }
}

.navegacion {

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity 0.5s linear, height 0.5s linear;

    @include tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    a {
        color: $azul;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type {
                margin-right: 0;
            }
        }

        &:hover {
            color: $rosa;
        }
    }
}

/* Menu de hamburguesa */
.mobile-menu {
    padding-top: 1rem;
    margin-bottom: 1rem;
    @include tablet {
        display: none;
    }
    img {
        width: 5rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}
