

.linea-tiempo {
    
    @include tablet {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        column-gap: 1rem;
    }
}
.bloque {
    border: 1px solid $gris;
    border-radius: 15%;
    margin-bottom: 1rem;
    display: block;
    align-items: center;
    justify-content: space-between;
    @include tablet {
        border: 1px solid $gris;
        padding: 1rem;
    }
}

.b {
    text-align: center;
    border-bottom: 1px solid $gris;

    &:last-of-type {
        border-bottom: none;
    }
}

.nombrealumno {
    text-align: right;
    padding-right: 3rem;
}