/* Contenedor Primer nivel */
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
.seccion-rosa {
    background-color: $rosa;
    border: 1px solid $rosa;
}
.seccion-amarillo {
    border: 1px solid $amarillo;
    background-color: $amarillo;
}
.seccion-blanco {
    background-color: $blanco;
}
.seccion-gris-claro {
    background-color: $gris;
}
.padd-seccion-color {
    padding: 3rem;
}

/* Contenedor Segundo nivel*/
.contenedor-2-secciones {
    padding: 2rem;
    @include tablet {
        @include grid(2,2rem);
    }
}
.contenido-centrado {
    max-width: 800px;
}
.contenido-mas-centrado {
    max-width: 400px;
}
.seccion-banner {
    margin-top: 4rem;
    margin-bottom: 4rem;
}
.margin-pequeno {
    margin: 1rem;
}

/* Contenedor de Tercer nivel */
.seccion {
    background-color: $blanco;
    border-radius: 15%;
    padding: 2rem;
    width: 100%;
}
.seccion-flex {
    align-items: center;
    border-radius: 15%;
    padding: 2rem;
    @include tablet {
        display: flex;
    }
}
.seccion-flex-column {
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
    align-items: center;
    justify-content: center;
    border-radius: 15%;
    padding: 2rem;
    margin-top: 2.5rem;
    width: 100%;
    @include tablet {
        margin-top: 0;
    }
}
.grid-20-80 {
    display: grid;
    grid-template-columns: 20% 80%;
    align-items: center;
    text-align: center;
    img {
        max-width: 2rem;
        margin: 0 auto;
    }
    i {
        text-align: right;
    }
    a {
        padding: 1rem;
        @include telefono {
            font-size: 3.8rem;
        }
    }
}
.alinear-derecha {
    display: flex;
    justify-content: flex-end;
}
.no-padd {
    padding: 0;
}
.no-padd-top {
    padding-top: 0;
}
.margin-cero {
    margin: 0;
}
.radius {
    border-radius: 15%;
}
.margin-pequeno {
    margin: 1rem;
}

/* Textos */

.titulo {
    font-weight: $black;
}
.center {
    text-align: center;
}
.left{
    text-align: left;
}
.supersize {
    font-size: 4.2rem;
}
.pequena {
    font-size: 1.5rem;
}
.p-azul {
    color: $azul;
}
.p-verde {
    color: $verde;
}
.p-amarillo {
    color: $amarillo;
}
.p-rosa {
    color: $rosa;
}
.p-gris {
    color: darken($gris, 50);
}
.p-blanco {
    color: $blanco;
}
.indicador {
    font-size: 3rem;
    font-weight: $bold;
    text-align: center;
}

.text-grid-flex {
    padding: 2rem;
    @include tablet {
        max-width: 50%;
    }
}

/* Imagenes */
.img{
    img {
        border: 1px solid rgb(247, 243, 243);
        border-radius: 15%;
    }
}

/* Otros */
.w-100 {
    width: 100%;
}
