/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #333333;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem 0;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/* Contenedor Primer nivel */
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.seccion-rosa {
  background-color: #ea268f;
  border: 1px solid #ea268f;
}

.seccion-amarillo {
  border: 1px solid #ffdc02;
  background-color: #ffdc02;
}

.seccion-blanco {
  background-color: #FFFFFF;
}

.seccion-gris-claro {
  background-color: #e1e1e1;
}

.padd-seccion-color {
  padding: 3rem;
}

/* Contenedor Segundo nivel*/
.contenedor-2-secciones {
  padding: 2rem;
}
@media (min-width: 768px) {
  .contenedor-2-secciones {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.contenido-centrado {
  max-width: 800px;
}

.contenido-mas-centrado {
  max-width: 400px;
}

.seccion-banner {
  margin-top: 4rem;
  margin-bottom: 4rem;
}

.margin-pequeno {
  margin: 1rem;
}

/* Contenedor de Tercer nivel */
.seccion {
  background-color: #FFFFFF;
  border-radius: 15%;
  padding: 2rem;
  width: 100%;
}

.seccion-flex {
  align-items: center;
  border-radius: 15%;
  padding: 2rem;
}
@media (min-width: 768px) {
  .seccion-flex {
    display: flex;
  }
}

.seccion-flex-column {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  align-items: center;
  justify-content: center;
  border-radius: 15%;
  padding: 2rem;
  margin-top: 2.5rem;
  width: 100%;
}
@media (min-width: 768px) {
  .seccion-flex-column {
    margin-top: 0;
  }
}

.grid-20-80 {
  display: grid;
  grid-template-columns: 20% 80%;
  align-items: center;
  text-align: center;
}
.grid-20-80 img {
  max-width: 2rem;
  margin: 0 auto;
}
.grid-20-80 i {
  text-align: right;
}
.grid-20-80 a {
  padding: 1rem;
}
@media (min-width: 480px) {
  .grid-20-80 a {
    font-size: 3.8rem;
  }
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.no-padd {
  padding: 0;
}

.no-padd-top {
  padding-top: 0;
}

.margin-cero {
  margin: 0;
}

.radius {
  border-radius: 15%;
}

.margin-pequeno {
  margin: 1rem;
}

/* Textos */
.titulo {
  font-weight: 900;
}

.center {
  text-align: center;
}

.left {
  text-align: left;
}

.supersize {
  font-size: 4.2rem;
}

.pequena {
  font-size: 1.5rem;
}

.p-azul {
  color: #0097db;
}

.p-verde {
  color: #aacf44;
}

.p-amarillo {
  color: #ffdc02;
}

.p-rosa {
  color: #ea268f;
}

.p-gris {
  color: #626262;
}

.p-blanco {
  color: #FFFFFF;
}

.indicador {
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
}

.text-grid-flex {
  padding: 2rem;
}
@media (min-width: 768px) {
  .text-grid-flex {
    max-width: 50%;
  }
}

/* Imagenes */
.img img {
  border: 1px solid #f7f3f3;
  border-radius: 15%;
}

/* Otros */
.w-100 {
  width: 100%;
}

/* Botones para pagina Admin para actualizar y borrar */
.boton-amarillo-block {
  background-color: #ffdc02;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #e8c700;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #bd0707;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #a40606;
  cursor: pointer;
}

/* Botones de compra */
.boton-verde-enrevision {
  background-color: #aacf44;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde-enrevision:hover {
  background-color: #9fc733;
  cursor: pointer;
}

.boton-verde {
  background-color: #aacf44;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 75%;
  margin: 0 auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
  border-radius: 4rem;
  font-size: 3rem;
}
.boton-verde:hover {
  background-color: #9fc733;
  cursor: pointer;
}

.boton-verde-100 {
  background-color: #aacf44;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
  margin: 0 auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
  border-radius: 4rem;
  font-size: 3rem;
}
.boton-verde-100:hover {
  background-color: #9fc733;
  cursor: pointer;
}

.boton-rosa {
  background-color: #ea268f;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 75%;
  margin: 0 auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
  border-radius: 4rem;
  font-size: 3rem;
}
.boton-rosa:hover {
  background-color: #e11683;
  cursor: pointer;
}

.boton-eje {
  background-color: #aacf44;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  font-size: 1.8rem;
  padding: 0.2rem;
  margin-top: 0.5rem;
  max-width: 90%;
  margin: 0 auto;
}
.boton-eje:hover {
  background-color: #9fc733;
  cursor: pointer;
}

/* UI */
.header {
  background-color: #FFFFFF;
}
.header h1 {
  text-align: center;
  font-weight: 700;
  line-height: 2;
  margin: 0;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra img {
  max-width: 20rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #0097db;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #ea268f;
}

/* Menu de hamburguesa */
.mobile-menu {
  padding-top: 1rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

/* Index y globales de Servicio*/
.servicio {
  margin-bottom: 2rem;
}

.img-servicio {
  margin: 0 auto;
  max-width: 15rem;
  max-height: 15rem;
}

.contenido-servicio h3, .contenido-servicio p {
  margin: 0;
}
.contenido-servicio h3 {
  font-weight: 700;
}
.contenido-servicio p {
  text-align: left;
}

.costo h3 {
  padding-top: 1rem;
}
.costo p {
  text-align: center;
  padding-top: 2rem;
  font-size: 3rem;
}

/* Pagina Servicios*/
.servicio-main {
  background-color: #FFFFFF;
  border: 1.5px solid #ea268f;
  border-radius: 2rem;
  padding: 2rem;
  margin-bottom: 2rem;
}
.servicio-main .img-servicio {
  margin: 0 auto;
  max-width: 15rem;
  max-height: 15rem;
}

.entrada-blog {
  border: 10px solid #0097db;
  margin-bottom: 2rem;
}

.texto-entrada {
  padding: 1rem;
}
.texto-entrada a {
  margin-top: 2rem;
}
.texto-entrada a:hover {
  color: #ea268f;
}

.link-docente {
  background-color: #ea268f;
  border-radius: 15%;
  width: 90%;
}

.footer {
  margin-top: 2rem;
  padding: 2rem 0 0 0;
  background-color: #FFFFFF;
  margin-bottom: 0;
  border-top: 1px solid #e1e1e1;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #aeaeae;
  text-align: center;
}
.footer p {
  margin: 1rem;
}

.redes {
  font-size: 4rem;
  margin-top: 2rem;
  margin: auto 0;
}
@media (min-width: 768px) {
  .redes {
    margin-top: 0;
  }
}
.redes #facebook {
  color: #0097db;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.redes #instagram {
  color: #ea268f;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.redes #whatsapp {
  color: #aacf44;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.contacto {
  border: 3px solid #ffdc02;
}
@media (min-width: 768px) {
  .contacto {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.info-contacto {
  display: flex;
  flex-direction: column;
  row-gap: 3rem;
  align-items: center;
  justify-content: center;
}
.info-contacto p {
  text-align: center;
  color: #959595;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #626262;
  border: 2px solid #ffdc02;
  padding: 1rem;
  margin-bottom: 1rem;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: 1rem;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #ffdc02;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario fieldset {
  border: #ffdc02;
}

.nuevo-link {
  padding: 0 2rem 0 2rem;
  background-color: #FFFFFF;
  border-radius: 2rem;
  max-width: 100%;
  max-height: 10rem;
  text-align: center;
  margin: 0 auto;
}

.historia {
  border: 1px solid #ffdc02;
}

.entrada-nosotros {
  border: 1px solid #ffdc02;
}

.texto-nosotros {
  border: 1px solid #ea268f;
  padding: 1.5rem;
}
.texto-nosotros h2 {
  color: #0097db;
}

/* Paginas Internas */
.alumno {
  border-bottom: 1px solid #bbbbbb;
  text-align: center;
}
.alumno:last-of-type {
  border-bottom: none;
}
@media (min-width: 768px) {
  .alumno {
    margin: 0;
    border: none;
  }
}

.info-alumno {
  border-radius: 0;
  border: 1px solid #e1e1e1;
  border-radius: 15%;
  padding: none;
  margin: 0.5rem;
}

@media (min-width: 480px) {
  .mat-alumno {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}

.barra-cuenta {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid #e1e1e1;
  text-align: center;
}
@media (min-width: 768px) {
  .barra-cuenta {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra-cuenta img {
  max-width: 10rem;
}
@media (min-width: 768px) {
  .barra-cuenta {
    text-align: right;
  }
}

@media (min-width: 768px) {
  .linea-tiempo {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    column-gap: 1rem;
  }
}

.bloque {
  border: 1px solid #e1e1e1;
  border-radius: 15%;
  margin-bottom: 1rem;
  display: block;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .bloque {
    border: 1px solid #e1e1e1;
    padding: 1rem;
  }
}

.b {
  text-align: center;
  border-bottom: 1px solid #e1e1e1;
}
.b:last-of-type {
  border-bottom: none;
}

.nombrealumno {
  text-align: right;
  padding-right: 3rem;
}

.bloque-alumno {
  display: block;
  padding: 2rem;
}

.eje {
  border: 1px solid #e1e1e1;
  border-radius: 15%;
  padding: 1.5rem;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .eje {
    display: grid;
    grid-template-columns: 12% 12% 12% 64%;
  }
}

.info-eje {
  padding: 1rem;
}
.info-eje p {
  font-size: 1.2rem;
}

