.encabezado-desafio {
    @include tablet {
        @include grid(3,0);
    }
}

.linea-eje {
    @include tablet {
        border: 1px solid $gris;
        padding-right: 3rem;
        padding-left: 3rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        column-gap: 1rem;
    }
}
.eje-desafio {
    border: 1px solid $gris;
    margin-bottom: 1rem;
    display: block;
    align-items: center;
    justify-content: space-between;
    @include tablet {
        border: none;
    }
}