
/* Index y globales de Servicio*/

.servicio {
    margin-bottom: 2rem;
}
.img-servicio {

    margin: 0 auto;
    max-width: 15rem;
    max-height: 15rem;
}
.contenido-servicio {
    
    h3, p {
        margin: 0;
    }
    h3 {
        font-weight: $bold;
    }
    p{
        text-align: left;
    }
}
.costo {
    h3 {
        padding-top: 1rem;
    }
    p {
        text-align: center;
        padding-top: 2rem;
        font-size: 3rem;
    }
}

/* Pagina Servicios*/

.servicio-main {
    background-color: $blanco;
    border: 1.5px solid $rosa;
    border-radius: 2rem;
    padding: 2rem;
    margin-bottom: 2rem;

    
    .img-servicio {

        margin: 0 auto;
        max-width: 15rem;
        max-height: 15rem;
    }
}



