
/* Botones para pagina Admin para actualizar y borrar */
.boton-amarillo-block {
    @include boton($amarillo, block);
}
.boton-rojo-block {
    @include boton($rojo, block);
    width: 100%;
}


/* Botones de compra */
.boton-verde-enrevision {
    @include boton($verde, inline-block);
}

.boton-verde {
    @include boton($verde, block);
    width: 75%;
    margin: 0 auto;
    margin-top: 2rem;
    margin-bottom: 2rem;
    border-radius: 4rem;
    font-size: 3rem;
}
.boton-verde-100 {
    @include boton($verde, block);
    width: 100%;
    margin: 0 auto;
    margin-top: 2rem;
    margin-bottom: 2rem;
    border-radius: 4rem;
    font-size: 3rem;
}
.boton-rosa {
    @include boton($rosa, block);
    width: 75%;
    margin: 0 auto;
    margin-top: 2rem;
    margin-bottom: 2rem;
    border-radius: 4rem;
    font-size: 3rem;
}
.boton-eje {
    @include boton($verde, block);
    font-size: 1.8rem;
    padding: .2rem;
    margin-top: .5rem;
    max-width: 90%;
    margin: 0 auto;
}