
.alumno {
    border-bottom: 1px solid darken($gris, 15);
        &:last-of-type {
            border-bottom: none;
        }
    text-align: center;
    @include tablet {
        margin: 0;
        border: none;
    }
}
.info-alumno {
    border-radius: 0;
    border: 1px solid $gris;
    border-radius: 15%;
    padding: none;
    margin: .5rem
}
.mat-alumno {
    
    @include telefono {
        @include grid(2, 0);
    }
}
