.clase {
    display: block;
    align-items: center;
    justify-content: center;
    border: 8px solid $rosa;
    border-radius: 15%;
    padding: 1rem;
}
.clase-info {
    max-width: 70%;
    margin: 0 auto;
    @include tablet {
        max-width: 50%;
    }
    p {
        text-align: left;
    }
}
.fecha-hora {
    margin: 0 auto;
    max-width: 40rem;
    margin-bottom: 5rem;
}