
.contacto {
    border: 3px solid $amarillo;
    @include tablet {
        @include grid(2, 2rem)
    }
}
.info-contacto {
    display: flex;
    flex-direction: column;
    row-gap: 3rem;
    align-items: center;
    justify-content: center;
    p {
        text-align: center;
        color: darken($gris, 30)
    }
}


