.alerta {
    margin: 1rem;
    padding: .5rem;
    text-align: center;
    color: $grisOscuro;
    font-weight: $bold;
    text-transform: uppercase;

    &.error {
        background-color: $amarillo;
    }
    &.exito {
        background-color: $verde;
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: $grisOscuro;

        th {
            color: $blanco;
            padding: 2rem;
        }
    }

}