
.bloque-alumno {
    display: block;
    padding: 2rem;
}
.eje {
    border: 1px solid $gris;
    border-radius: 15%;
    padding: 1.5rem;
    margin-top: 1rem;
    @include tablet {
        display: grid;
        grid-template-columns: 12% 12% 12% 64%;
    }
}
.info-eje {
    
    padding: 1rem;
    p {
        font-size: 1.2rem;
    }
}
.titulo-tabla {
    min-width: 5rem;
}

.b-bloque {
    padding: 1rem;
    text-align: center;
    border-bottom: 1px solid $gris;
    @include tablet {
        border-bottom: none;
    }
    
    &:last-of-type {
        border-bottom: none;
    }
}
.i-b-bloque {
    @include grid(2, 0);
    text-align: center;
    border-bottom: 1px solid $gris;
        &:last-of-type {
            border-bottom: none;
        }
    @include tablet {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-right: 1px solid $gris;
        border-bottom: none;
        &:last-of-type {
            border-right: none;
        }
    }
}
