.formulario {
    p {
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;

    }

    legend {
        font-size: 2rem;
        color: darken($gris, 50);
        border: 2px solid $amarillo ;
        padding: 1rem;
        margin-bottom: 1rem;
    }

    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }

    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: 1rem;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $amarillo;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    textarea {
        height: 20rem;
    }

    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 auto;
    }
    input[type="radio"] {
        margin: 0;
        width: auto;
    }
    fieldset {
        border: $amarillo;
    }
}