.footer {
    margin-top: 2rem;
    padding: 2rem 0 0 0;
    background-color: $blanco;
    margin-bottom: 0;
    border-top: 1px solid $gris;

    .navegacion {
        visibility: visible;
        opacity: 1;
        height: auto;
    }

    .copyright {
        color: darken($gris, 20);
        text-align: center;
    }
    p {
        margin: 1rem;
    }
}

.redes {
    font-size: 4rem;
    margin-top: 2rem;
    margin: auto 0;
    @include tablet {
        margin-top: 0;
    }
    #facebook {
        color: $azul;
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }
    #instagram {
        color: $rosa;
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }
    #whatsapp {
        color: $verde;
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }
}