.barra-cuenta {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid $gris;
    text-align: center;
    

    @include tablet {
        flex-direction: row;
        justify-content: space-between;
    }
    img {
        max-width: 10rem;
    }
    @include tablet {
        text-align: right;
    }
}